body {
    background-color: rgba(239, 239, 239, 0.84);
}
ul {
    padding-left: 0;
    list-style-type: none;
}
li {
    padding: 6px;
}

.actionMenu {
    position: fixed;
    top: 0;
    left: 0;
    width: 200px;
    height: 100%;
    background-color: rgba(54, 54, 54, 0.58);
    color: white;
}

.centered {
    text-align: center;
}


.button {
    position: fixed;
    color: white;
    border: none;
    padding: 8px;
    height: 40px;
    width: 140px;
    border-radius: 8px;
    cursor: pointer;
}

.backButton {
    background-color: rgb(53, 91, 241);
    top: 10px;
    left: 30px;
}

.addButton {
    background-color: #4CAF50;
    top: 70px;
    left: 30px;
}

.loadButton {
    background-color: rgba(0, 0, 0, 0.38);
    top: 290px;
    left: 30px;
}

.saveButton {
    background-color: rgba(54, 54, 54);
    top: 350px;
    left: 30px;
}

.infoButton {
    background-color: rgba(241, 225, 53, 0.66);
    top: 410px;
    left: 30px;
}

.manageButton {
    background-color: rgba(0, 0, 0, 0.84);
    top: 470px;
    left: 30px;
}

.settingsButton {
    background-color: rgba(0, 0, 0, 0.84);
    top: 470px;
    left: 30px;
}

.settingsMenu {
    position: fixed;
    top: 530px;
    left: 30px;
}

.hidden {
    display: none;
}

.folder {
    display: inline-block;
    margin: auto;
    position: relative;
    background-color: #c0cad3;
    border-radius: 0 6px 6px 6px;
    width: 120px;
    height: 90px;
    box-shadow: 4px 4px 7px rgba(0, 0, 0, 0.59);
    margin-bottom: -8px;
    margin-top: 12px;
}

.folder:before {
    content: '';
    width: 50%;
    height: 0.4em;
    border-radius: 5px 20px 0 0;
    background-color: #858f98;
    position: absolute;
    top: -0.5em;
    left: -2px;
}

.link {
    display: inline-block;
    margin: auto;
    position: relative;
    background-color: #c0e2ff;
    border-radius: 90px;
    width: 120px;
    height: 120px;
    box-shadow: 4px 4px 7px rgba(0, 0, 0, 0.59);
    margin-bottom: -8px;
    margin-top: 12px;
}
