.PasteLinkInput {
    /*display: flex;*/
    /*flex-direction: column;*/
    /*align-items: center;*/
    margin-top: 20px;
}

.PasteLinkInput .link-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    margin-top: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    width: 500px;
}

.PasteLinkInput .link-box a {
    text-decoration: none;
    color: #000;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.PasteLinkInput .link-box button {
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    background-color: #da190b;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.PasteLinkInput .link-box button:hover {
    background-color: #c91f1f;
}

.PasteLinkInput label {
    display: block;
    margin-bottom: 5px;
}