.PasteImageInput {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    margin-top: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    width: 220px;
    /*margin: 0 auto;*/
}

.PasteImageInput input {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    width: 110px;
    margin-bottom: 10px;
    margin-top: 10px;
    /*text-align: center;*/
}

.PasteImageInput img {
    max-width: 90%;
    max-height: 90%;
    object-fit: cover;
    margin-bottom: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.PasteImageInput button {
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    background-color: #da190b;
    color: white;
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 10px;
    transition: background-color 0.3s ease;
}

.PasteImageInput button:hover {
    background-color: #c91f1f;
}

.PasteImageInput img.full-size {
    position: fixed; /* Position the image relative to the viewport */
    top: 50%; /* Center the image vertically */
    left: 50%; /* Center the image horizontally */
    transform: translate(-50%, -50%); /* Offset the image by half its height and width to truly center it */
    width: auto; /* Remove the width restriction */
    height: auto; /* Remove the height restriction */
    max-width: 90%; /* Remove the maximum width restriction */
    max-height: 90%; /* Remove the maximum height restriction */
    z-index: 1000; /* Ensure the image appears above other elements */
}

.PasteImageInput .input-delete-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}